.feedback-form {
  max-width: 480px;
  height: 450px;
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  background: none;
  border: 2px solid #ffffff80;
  border-radius: 20px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #fff;
  border-bottom: 2px solid #fff;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  width: 100%;
  height: 40px;
  color: #fff;
  background: none;
  border-color: #fff;
  border-radius: 40px;
  padding: 8px 12px;
  font-size: 1em;
  font-weight: 600;
  transition: all .2s ease-in-out;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

input, textarea {
  font: inherit;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 8px;
}

textarea {
  resize: none;
}

.feedback-form button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

.feedback-form button:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

